<template>
    <div >
        <table class='table table-striped table-hover'>
            <thead>
                <tr>
                  <th>#</th>
                  <th>Ver</th>
                  <th>Numero</th>
                  <th>Total</th>
                    <th >Eliminar</th> 
                </tr>     
            </thead>
            <tbody>
                <tr  v-for="num, index in getNumerosCli" :key="num._id">
                      <td>{{index + 1}}</td>
                       <td>
                          <router-link 
                              class="btn btn-info"
                              :to="{name:'rifa-facturar', params:{id:num._id}}"
                          >
                              VER
                          </router-link>
                      </td> 
                    <td>{{num.numero}}</td>
                     <td>{{num.total}}</td> 
       
                      <td>
                        <button 
                            class='btn btn-danger'
                            :disabled="getIsLoading"
                            @click="deseleccionar(num._id)"
                        >
                            Eliminar
                        </button>
                    </td> 
                </tr>
                        
            </tbody>
        
        </table>
      </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {

    computed:{
        ...mapGetters('numeros',['getNumerosCli', 'getIsLoading'])
    },
    methods:{
        ...mapActions('numeros',['deseleccionarNumero']),
        deseleccionar(id){
            var numero = {
                id,
                ocupado: false,
                cliente: null
            }
            this.deseleccionarNumero(numero)
        }
    }

}
</script>

